#Service2 .Process_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    margin-top: 6rem;
    padding-top: 1rem;
    padding-bottom: 8rem;
    margin-bottom: 5rem;
    background-color: rgba(173, 216, 230, 0.243);
}

#Service2 .process_containt {
    width: 80%;
}

#Service2 .process_containt h2 {
    position: relative;
    font-size: clamp(1.3rem, 1.7vw, 10vw);
    color: rgba(0, 0, 0, 0.843);
}


#Service2 .process_containt h2::before,
#Service2 .process_containt h2::after {
    content: "";
    position: absolute;
    width: 5rem;
    height: .152rem;
    border-radius: 1rem;
    bottom: -0.2rem;
    left: 0;
    background-color: #3b61dd;
}

#Service2 .process_containt h2::after {
   bottom: -.7rem;
   width: 3rem;
   
}




#Service2 .process_containt .process {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(12rem, 1fr));
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-top: 4rem;
    row-gap: 1rem;
}






#Service2 .process_containt .process .single_process {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


#Service2 .process_containt .process .single_process p {
    text-align: center;
    font-weight: 600;
    font-size: 1.1rem;
    margin-top: 1rem;
    width: 80%;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

#Service2 .process_containt .process .single_process .icon_box {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 8rem;
    height: 8rem;
    border-radius: 10rem;
    border: 2px solid #3b61dd;
}

#Service2 .process_containt .single_process .icon_box i {
    font-size: 3rem;
}

#Service2 .process_containt .single_process .icon_box .bi-chevron-double-right {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    cursor: pointer;
    font-size: 1rem;
    right: -1.4rem;
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 10rem;
    border: 1px solid #3b61dd;
    background-color: #3b61dd;
    color: white;
    
} 


@media (max-width: 501px) {
    #Service2 .Process_container {
    padding-bottom: 4rem;
}


    #Service2 .process_containt {
    width: 90%;
}


    #Service2 .process_containt .process {
        grid-template-columns: 1fr 1fr;
    }

    #Service2 .process_containt .process .single_process .icon_box {
    width: 30vw;
    height: 30vw;
}

#Service2 .process_containt .single_process .icon_box i {
    font-size: 10vw;
}

#Service2 .process_containt .single_process .icon_box .bi-chevron-double-right {
    font-size: 4vw;
    right: -5vw;
    width: 9vw;
    height: 9vw;    
} 
}
























