#Service5 .service_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 2rem;
    padding-bottom: 4rem;
    background-color: white;
}

#Service5 .service_containt {
    display: flex;
    box-sizing: border-box;
    width: 80%;
    gap: 2rem;
    padding: 2rem ;
    border-radius: .4rem;
    background-color: rgba(173, 216, 230, 0.131);
    border: 1px solid #3b61dd55;
}

#Service5 .service_containt > div {
    width: 100%;
    padding-left: 1rem;
    border-left: 1px solid #3b61dd;
}

#Service5 .service_containt > div p ,
#Service5 .service_containt > div h4 {
    margin-top: 0;
}

#Service5 .service_containt > div p  {
    font-size: clamp(1rem, 1.3vw, 10vw);
    line-height: 1.6;    

}

#Service5 .service_containt > div h4 {
   position: relative;
   font-size: 1.2rem;
}

#Service5 .service_containt > div h2 {
    position: relative;  
    margin-top: 0;
    font-size: clamp(1.8rem, 2.4vw, 10vw);
    margin-bottom: 3rem;
    color: rgb(52, 52, 52);  
}

#Service5 .service_containt > div h2::before,
#Service5 .service_containt > div h2::after {
    content: "";
    position: absolute;
    width: 5rem;
    height: .152rem;
    border-radius: 1rem;
    bottom: -0.2rem;
    left: 0;
    background-color: #3b61dd;
}

#Service5 .service_containt > div h2::after {
   bottom: -.7rem;
   width: 3rem;
   
}





#Service5 .service_containt ul li {
    font-size: clamp(1rem, 1.3vw, 10vw);
    line-height: 1.5;  
    margin: .5rem 0;
    font-weight: 500;
}

#Service5 .service_containt > div h4::before {
    content: "";
    position: absolute;
    width: .6rem;
    height: .6rem;
    border-radius: 2rem;
    left: -1.32rem;
    bottom: .35rem;
    box-shadow: 0 0 1rem #3b61ddcd;
    background-color: #3b61dd;
}


@media (max-width: 800px) {
    #Service5 .service_containt {
        flex-direction: column;
    }
}

@media (max-width: 600px) {
    #Service5 .service_containt {
        width: 90%;
        padding: 2rem 1rem;
        padding-right: 1.5rem;
    }

 



}

@media (max-width: 400px) {
    #Service5 .service_containt > div h4::before,
#Service5 .service_containt > div h1::before {
    left: -.91rem;
    bottom: .35rem;
}

   #Service5 .service_containt > div {
    padding-left: .6rem;
}

 #Service5 .service_containt {
        padding-left: .5rem;
    }
}

