#service4 .service_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 5rem;
    padding-bottom: 10rem;
    background-color: rgba(173, 216, 230, 0.412);

}

#service4 .service_containt {
    width: 80%;
}


/* style for the title section */
#service4 .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 4rem;
    margin-bottom: 4rem;
}

#service4 .title_section h1 {
    font-size: clamp(1.5rem, 3vw, 10vw);
    margin: .5rem 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',  sans-serif;

}


#service4 .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.3vw, 10vw);
    line-height: 1.6;
    width: 90%;
    margin: 0;
    color: rgb(100, 100, 100);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',  sans-serif;

    /* color: red; */
}

#service4 .title_section button {
    cursor: pointer;
    border: none;
    width: 15rem;
    padding: .71rem 0;
    border-radius: 10rem;
    font-size: 1rem;
    margin-top: 1rem;
    border: .7rem solid white;
    background-color: #3b61dd;
    color: white;
}


#service4 .offer_list {
    display: grid;
    grid-template-columns: repeat(auto-fill, 15rem);
    justify-content: center;
    gap: 2rem;
}

#service4 .single_offer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    text-align: center;
    border: .5rem solid white;
    padding: 2rem 0;
    background-color: white;
    border-radius: 1rem ;
    
}

#service4 .single_offer:hover {
    scale: 1.1;
}

#service4 .single_offer i {
    font-size: 2.5rem;
    color: rgb(75, 75, 75);
}

#service4 .single_offer p {
    margin-bottom: 0;
}


@media (max-width: 501px) {
    #service4 .offer_list {
        grid-template-columns: 1fr;
    }
}