#About8 .About_page_container {
    display: flex;
    justify-content: center;
    gap: 2rem;
    padding-top: 10rem ;
    padding: 6rem 8vw;
    background-color: white;
    box-sizing: border-box;
}

#About8 .About_page_container .image_container {
    display: flex;
    justify-content: center;
    width: 40%;
}
#About8 .About_page_container .image_border {
    overflow: hidden;
   width: 30vw;
   height: 30vw;
   border-radius: 20rem;
   border-top-right-radius: 0;
   border: 1rem solid white;
   box-shadow: 0 0 1rem rgba(173, 216, 230, 0.743);

}

#About8 .About_page_container .about_img_profil {
    transform: translateY(-3rem);
    filter: contrast(130%);
    object-fit: cover;
    width: 100%;
    height: 150%;
    border-radius: 20rem;
    border-top-right-radius: 0;
    background-color: white;
    background-color: rgba(173, 216, 230, 0.314);
    

}

#About8 .about_info_container {
    width: 50%;
}

#About8 .about_info_container h2 {
    font-size: 2rem;
    margin: 0;
    color: rgb(58, 58, 58);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',  sans-serif;

}

#About8 .about_info_container .web_dev {
    font-weight: bold;
    font-size: 1.2rem;
    margin: .6rem 0;
    color: #3b61dd;
}

#About8 .about_info_container .desc {
    width: 100%;
    font-size: clamp(1rem, 1.3vw, 10vw);
    line-height: 1.6;
    color: rgba(0, 0, 0, 0.781);
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',  sans-serif; */

}

#About8 .about_info_container .desc hr {
    border: none;
    margin: .7rem;
}

#About8 .social_media_link {
    display: flex;
    justify-content: start;
    align-items: center;
    gap: 1rem;
    width: 100%;
}

#About8 .social_media_link i {
    font-size: 1.4rem !important;
    color: #3b61dd;
}

#About8 .about_info_container .about_button_section {
    display: flex;
    justify-content: center;
    gap: 2rem;
    padding: 2rem 0;
    padding-top: 0;
    
}

#About8 .about_info_container .about_button_section button {
    background-color: transparent;
    cursor: pointer;
    width: 10rem;
    padding: .5rem 0;
    border-radius: .3rem;
    border-radius: 10rem;
    font-size: 1rem;
    font-weight: 600;
    border: 1px solid #3b61dd;
    color: #3b61dd;
}

#About8 .about_info_container .about_button_section .download_cv {
    background-color: #eef2ff;
    font-weight: 500;
}

@media(max-width: 1000px) {
    #About8 .About_page_container {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    #About8 .image_container {
        justify-content: center;
        align-items: center;
        width: 100% !important;
    }



     #About8 .about_info_container {
        width: 100%;
     }


     #About8 .About_page_container .image_border {
   width: 25rem;
   height: 25rem;
   }

     
}

@media(max-width: 501px) {
    #About8 .About_page_container {
        padding: 1rem;
        padding-top: 10rem !important;
    }


    #About8 .about_info_container .about_button_section {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: .8rem;
    }



     #About8 .about_info_container h2 {
        font-size: 1.5rem;
     }


         #About8 .About_page_container .image_border {
   width: 80vw;
   height: 80vw;
   }
}


@media (max-width: 400px) {
    #About8 .About_page_container .about_button_section {
        flex-direction: column;
    }

    #About8 .About_page_container .social_media_link {
        justify-content: center;
    }
}



@media(max-width: 300px) {
    #About8 .about_info_container .about_button_section {
        flex-direction: column;
        
    }

    #About8 .About_page_container .about_img_profil {
   transform: translateY(-2rem);
    

}
}


#About8 .fiverr_logo,
#About8 .upwork_logo {
    width: 1.782rem !important;
    height: 1.782rem !important;
    object-fit: cover !important;
}

#About8 .bi-linkedin {
    color: rgb(0, 138, 230) !important;
}

#About8 .bi-whatsapp {
    color: rgb(7, 177, 49) !important;
}