 #Restaurant {
    display: flex;
    justify-content: center;
    align-content: center;
    width: 100vw ;
}

#Restaurant .navbar_containt {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    top: 0;
    left: 0rem;
    right: 0rem;
    z-index: 10;
    padding: 0rem 2rem;
    background-color: orange;

}




/* ///////////////////////////////////////////////////////////////////////// */
/* Logo section */
#Restaurant .logo_section {
    display: flex;
    align-items: center;
    justify-content: space-between;
    
}

#Restaurant .logo_section .logo {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    gap: 1rem;
}


#Restaurant .logo_section img {
   width: 4.5rem;
}

@font-face {
    font-family: ultra-regulare;
    src: url(./Font-familly/Flamenco-Regular.ttf);
}

#Restaurant .logo_section h3 {
    font-size: 1.47rem;
    font-family: ultra-regulare;
    margin: 0;
    color: rgb(110, 47, 47);
}

#Restaurant .logo_section .menu_icon {
    display: none;
}







/* /////////////////////////////////////////////////////////////////////////////////// */
/* Link section */
#Restaurant .link_section ul {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

#Restaurant .link_section ul li {
    list-style-type: none;
}

#Restaurant .link_section ul li a {
    text-decoration: none;
    font-weight: bold;
    white-space: nowrap;
    color: rgb(110, 47, 47);
}

 #Restaurant .bi-cart-dash {
     cursor: pointer;
    font-size: 1.35rem;
    border-radius: 9rem;
    padding: .2rem .5rem;
    color: brown;
    background-color: rgb(241, 205, 151);
}

#Restaurant .bi-heart-fill.navBar {
    font-size: 1.5rem;
}

#Restaurant .bi-heart-fill.mobile {
    font-size: 1.5rem !important;
}

#Restaurant .link_section li a:hover {
    border-bottom: 2px solid  rgb(110, 47, 47);
}


/* /////////////////////////////////////////////////////////////////////////////// */

@media (max-width: 800px) {
    #Restaurant .navbar_containt {
        flex-direction: column;
        justify-content: start;
        overflow: hidden;
        background-color: orange;
        left: 0;
        right: 0;
        padding: 0 2rem;
        height: 3rem;
        /* width: 100%; */
    }


    #Restaurant.active .navbar_containt {
        height: 100vh;
    }


    #Restaurant .logo_section img {
        width: 3.64rem;
     }




#Restaurant   .link_section ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
    padding: 0;
    margin-top: 3rem;
    margin-bottom: 3rem;
    width: 100%;
    }


    #Restaurant .logo_section {
        justify-content: center;
        align-items: center;
        width: 100%;
    }

    #Restaurant .logo_section .logo {
        justify-content: start;
        width: 100%;
    }


    #Restaurant .logo_section .menu_icon {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 1rem;
    }

    #Restaurant .logo_section .menu_icon i {
        cursor: pointer;
        font-size: 2rem;
        transition: .3s;
        color: rgb(109, 36, 36);
     }

     #Restaurant .logo_section .menu_icon .bi-cart-dash {
        font-size: 1.4rem;
     }

     #Restaurant .logo_section .menu_icon i:hover {
        color: red;
     }


    
    #Restaurant .cart_notif {
        left: -.40rem !important;
        top: -.5rem !important;
        display: none;
    }

    #Restaurant .fav_notif {
        left: -.80rem !important;
        top: -.4rem !important;
        
    }

    /* #Restaurant .navbar_containt ul li div {
        display: none !important;
    } */

    #Restaurant .fav_notif_box,
    #Restaurant .cart_notif_box {
        display: none;
    }


}

@media (max-width: 600px) {
    #Restaurant .logo_section h3 {
        font-size: 1.142rem;

    }

    #Restaurant .logo_section img {
        width: 3rem;
     }




}

 @media (max-width: 400px) {
#Restaurant .logo_section h3 {
        font-size: .81rem;
        transform: translateY(.1rem);

    }

    #Restaurant .logo_section img {
        width: 2rem;
     }
}





#Restaurant .fav_notif_box {
  position: relative;
}

#Restaurant .fav_notif, .cart_notif {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .8rem;
    width: 1.3rem;
    height: 1.3rem;
    border-radius: 5rem;
    top: -.81rem;
    left: -.81rem;
    color: brown;
    background-color: rgba(255, 255, 255, 0.521);
}

#Restaurant .cart_notif_box {
    position: relative;
}

#Restaurant .cart_notif {
    left: -.6rem;
}








