/* Page container */
#Home .Home_page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 1.5rem;
    padding-bottom: 10rem;
    background-color: rgb(233, 245, 253);

}


#Home .Home_page input {
    width: 40vw;
    height: 2rem;
    border-radius: 10rem;
    border: none;
    padding-left: 2rem;
    margin-bottom: 2rem;
    background-color: white;
    font-size: 1.1rem;
    text-align: center;
    box-shadow: 0 0 1rem rgba(173, 216, 230, 0.581);
}

#Home .Home_page input:focus {
    outline: none;
}

#Home .home_containt {
    display: grid;
    grid-template-columns: repeat(auto-fill, 20vw);
    justify-content: center;
    align-items: center;
    width: 80%;
    gap: 2rem;
}


/* Profil section */
#Home .Home_page .img_box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#Home .Home_page .img_box img {
    object-fit: cover;
    width: 8vw;
    min-width: 10rem;
    height: 8vw;
    min-height: 10rem;
    border-radius: 100rem;
    background-color: white;
    box-shadow: 0 0 1rem rgba(173, 216, 230, 0.581);
}

#Home .Home_page .img_box h3 {
    margin: 0.1rem;
    font-size: clamp(1rem, 2vw, 10vw);
    color: rgb(33, 55, 113);
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

#Home .Home_page .img_box p {
    color: rgb(78, 78, 78);
    margin: 0rem;
    font-size: 1.1rem;
    font-weight: 600;
    /* color: red; */

}


/* Page title */

#Home .Home_page .Title {
    text-align: center;
    font-size: clamp(1.2rem, 2.2vw, 10vw);
    width: 80%;
    margin-top: 1rem;
    font-family: Arial, Helvetica, sans-serif;
    margin-bottom: 2rem;
    color: rgb(33, 55, 113);
}

#Home .Home_page .total_component {
    margin-top: .2rem;
    margin-bottom: 0;
    font-size: 1rem;
    color: gray;
}



/* Single items */

#Home .Home_page .single_section_box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    border-radius: 1rem;
    background-color: white;
    box-shadow: 0 0 1rem rgba(173, 216, 230, 0.681);
}



#Home .home_containt .single_section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 93%;
    margin: .71rem 0;
    border-radius: .61rem;
    background-color: white;

}

#Home .home_containt .single_img_box {
    overflow: hidden;
    width: 100%;
    height: 100%;
}

#Home .home_containt .single_section img {
    object-fit: cover;
    cursor: pointer;
    width: 100%;
    height: 10rem;
    transition: .3s;
}

#Home .home_containt .single_section img:hover {
    scale: 1.051;
}

.home_containt .single_section h1 {
    font-size: clamp(1.2rem, 1.8vw, 10vw);
    margin: .2rem;
    color: rgb(53, 53, 53);
}


#Home .home_containt .single_section h3 {
    font-size: 1rem;
    margin: 0rem;
    color: green;
}

#Home .home_containt .single_section .button {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    border: none;
    width: 100%;
    height: 2rem;
    margin-top: .5rem;
    border-radius: .5rem;
    color: white;
    background-color: #3b61dd;

}


/* Media queries */

@media (max-width: 1320px) {
    #Home .home_containt {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media (max-width: 1000px) {
    #Home .home_containt {
        grid-template-columns: 1fr 1fr;
        gap: .8rem !important;
    }
}

@media (max-width: 600px) {


    #Home .home_containt {
        width: 85%;
        gap: 2rem;
    }   

    #Home .Home_page input {
        width: 80vw;
    }

    #Home .Home_page .Title {
     margin-bottom: 2rem;
    }


    #Home .home_containt .single_section img {
        height: 8rem;
    }


    #Home .home_containt .single_section h3 {
        font-size: .91rem;
        margin: .2rem;

    }
}



@media (max-width: 400px) {
    #Home .home_containt {
        grid-template-columns: repeat(auto-fill, 90vw);
        gap: 2rem !important;
    }
}