#Footer2 .Footer_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    color: white;
}

#Footer2 .footer_containt {
    position: relative;
    background-size: cover;
    background-position: bottom;
    background-repeat: no-repeat;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.973), rgba(0, 0, 0, 0.479)), url("./Image/footer_img_1.jpg");
    width: 100%;

}



#Footer2 .footer_containt .footer_details_containt {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    padding: 0 3rem;
    margin-bottom: 10rem;

}

#Footer2 .footer_containt  .footer_section_1 {
    width: 50%;
}

#Footer2 .footer_containt .footer_section .logo {
    display: flex;
    align-items: center;
    gap: 1.5rem;
}


#Footer2 .footer_containt .footer_section .logo img {
    width: 6rem;
} 

@font-face {
    font-family: footer_font;
    src: url(./Font-familly/Flamenco-Regular.ttf);
}

#Footer2 .footer_containt .footer_section .logo h3 {
    font-size: 3.1rem;
    font-size: clamp(1.5rem, 3.3vw, 4vw);
    font-family: footer_font;
    color: rgba(255, 255, 255, 0.753);
}

#Footer2 .footer_containt .footer_section .description {
    font-size: 1.2rem;
    font-size: clamp(1rem, 1.2vw, 3vw);

    color: rgba(255, 255, 255, 0.788);
    line-height: 1.8rem;
}

/* footer_section_2 */
#Footer2 .footer_containt .footer_section_2 {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    width: 35%;
    padding-top: 6rem;
    
}

#Footer2 .footer_containt .footer_section_2 .personal_info {
    display: flex;
    align-items: center;
    gap: 1.4rem;
    font-size: clamp(1rem, 1.4vw, 3vw);

}



#Footer2 .footer_containt .footer_section_2 i {
    font-size: clamp(1.3rem, 1.6vw, 3vw);

}

#Footer2 .footer_containt .footer_section_2 p {
    margin: 0;
    color: rgb(255, 255, 255);
}


/* copywrite_container */
#Footer2 .footer_containt .copywrite_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 3rem;
    position: absolute;
    bottom: 2rem;
    left: 2rem;
    right: 2rem;


}

#Footer2 .footer_containt .copywrite_container h5 {
    text-transform: uppercase;
    font-size: .9rem;
    margin: 0;
    color: rgba(197, 197, 197, 0.795);
}

#Footer2 .footer_containt .copywrite_container .right {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2.5vw;
}

#Footer2 .footer_containt .copywrite_container .right i {
    cursor: pointer;
    font-size: clamp(1.3rem, 1.6vw, 3vw);
    transition: .25s;
}

#Footer2 .footer_containt .copywrite_container .right i:hover {
    color: orangered;

}


/* Media Query */
@media (max-width: 1000px) {
    #Footer2 .footer_containt .footer_details_containt {
        flex-direction: column;
    }

    #Footer2 .footer_containt .footer_section {
        width: 100%;
    }

    #Footer2 .footer_containt .footer_section_2 {
        padding-top: 0;
    }
}


@media (max-width: 800px) {

    #Footer2 .footer_containt .footer_section .logo img {
    width: 3rem;
    } 


    #Footer2 .footer_containt .copywrite_container {
        flex-direction: column-reverse;
        align-items: start;
        gap: 1rem;
        left: 0rem;

    
    }



}


@media (max-width: 501px ){
    #Footer2 .footer_containt .footer_details_containt {
        padding: 1rem;
    
    }

    #Footer2 .footer_containt .copywrite_container .right {
        gap: 6vw;
    }
}

