#About_7 .About_container  {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 8rem;
    padding-bottom: 8rem;
    background-color: white;
}

#About_7 .about_containt {
    display: flex;
    gap: 2rem;
    width: 80%;
}


/* image_section */
#About_7 .image_section {
    overflow: hidden;
    width: 27vw;
    min-width: 18rem;
    height: 27vw;
    min-height: 18rem;
    border: 1rem solid white;
    box-shadow: 0 0 1rem rgba(173, 216, 230, 0.724);
    border-radius: 20rem 0rem 20rem 20rem;
    background-color: rgba(173, 216, 230, 0.19);
}


#About_7 .image_section img {
    object-fit: cover;
    transform: translateY(-5rem);
    filter: contrast(120%);
    width: 100%;
    height: 130%;
    
}


/* description_wrapper */
#About_7 .description_wrapper {
    width: 70%;
}

#About_7 .description_wrapper h4{
   margin: 0;
   color: #3b61ddaf;
   font-size: 1.2rem;
}

#About_7 .description_wrapper h2{
   margin-top: .2rem;
   font-size: clamp(1.8rem, 2.5vw, 10vw);
}

#About_7 .description_wrapper  p {
    font-size: clamp(1rem, 1.3vw, 10vw);
    line-height: 1.6;
    color: rgb(75, 75, 75);
}

#About_7 .description_wrapper > div {
    display: flex;
}

#About_7 .description_wrapper > div > div {
    width: 100%;
}

#About_7 .description_wrapper .personal_info {
    display: flex;
    gap: 0rem;
    padding: 1rem;
    border-radius: .3rem;
    margin-top: 2rem;
    border: 1px solid #3b61dd5c;
    background-color: rgba(173, 216, 230, 0.164);
}

#About_7 .description_wrapper .personal_info > div div{
   display: flex;
}

#About_7 .description_wrapper .personal_info > div div p:first-child{
   font-weight: bold;
   min-width: 5.5rem;
}



#About_7 .description_wrapper .personal_info > div:first-child {
   min-width: 22.5rem;
}

#About_7 .description_wrapper .personal_info > div:last-child {
   min-width: 14rem;

}








#About_7 .description_wrapper .personal_info p{
   margin: .2rem 0;
}



#About_7 .description_wrapper .btn_section {
    display: flex;
    gap: 1rem;
    margin-top: 2rem;

}



#About_7 .description_wrapper button {
    background-color: #3b61dd;
    padding: .5rem 1.5rem;
    border-radius: .4rem;
    color: white;
    cursor: pointer;
    border: none;
}



#About_7 .description_wrapper .btn_section .read_more{
    background-color: transparent;
    border: 1px solid #3b61dd;
    color: #3b61dd;
}


@media (max-width: 1260px) {
    #About_7 .description_wrapper > div {
    flex-direction: column;
    gap: 1.5rem;
}

#About_7 .description_wrapper button {
    width: 100%;
}
}

@media (max-width: 850px) {
    #About_7 .about_containt {
        flex-direction: column;
        gap: 1rem;
    }

     #About_7 .description_wrapper > div {
    flex-direction: row;
    }


#About_7 .image_section_box  {
    display: flex;
    justify-content: center;
    align-items: center;
}

#About_7 .image_section {
     width: 20rem;
    min-width: 8rem;
    height: 20rem;
    min-height: 8rem;
   
}

#About_7 .image_section img {
    transform: translateY(-3rem);
    
}



#About_7 .description_wrapper {
    width: 100%;
}

#About_7 .description_wrapper button {
    width: auto;
}

#About_7 .description_wrapper .personal_info > div:first-child {
   min-width: 20rem;
}

}

@media (max-width: 750px) {
    #About_7 .about_containt {
    display: flex;
    width: 90%;
}


    #About_7 .description_wrapper > div {
    flex-direction: column;
}

#About_7 .description_wrapper button {
    width: 100%;
}
}


@media (max-width: 400px) {
    #About_7 .about_containt {
    width: 95%;
}


#About_7 .description_wrapper .personal_info {
    padding: 1rem .5rem;
}



    #About_7 .image_section {
     width: 70vw;
    height: 70vw;
   
}

#About_7 .description_wrapper .personal_info > div:first-child {
   min-width: 2rem;
}

#About_7 .description_wrapper .personal_info > div:last-child {
   min-width: 3rem;
}
}