#About2 .about_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 10rem;
    padding-bottom: 10rem;
    background-color: rgba(173, 216, 230, 0.372);
}

#About2 .about_containt {
    width: 80%;
}





/* style for the title section */
#About2 .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 0rem;
    margin-bottom: 4rem;
}

#About2 .title_section h1 {
    font-size: clamp(1.5rem, 3vw, 10vw);
    margin: .5rem 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',  sans-serif;

}


#About2 .title_section p {
    text-align: center;
    line-height: 1.7rem;
    width: 90%;
    margin: 0;
    color: rgb(100, 100, 100);
    font-size: clamp(1rem, 1.3vw, 4vw);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',  sans-serif;

    /* color: red; */
}

#About2 .title_section button {
    cursor: pointer;
    border: none;
    width: 15rem;
    padding: .71rem 0;
    border-radius: 10rem;
    font-size: 1rem;
    margin-top: 1rem;
    border: .7rem solid white;
    background-color: #3b61dd;
    color: white;
}






#About2 .section2 {
    position: relative;
    display: flex;
    gap: 1rem;
}


#About2 .image_box {
    width: 37vw;
    height: 22rem;
    border: .71rem solid white;
    border-radius: .7rem;
}


#About2 .image_box img {
    object-fit: cover;
    width: 25vw;
    height: 100%;
}


#About2 .description {
    position: absolute;
    right: 0;
    top: 4rem;
    width: 66%;
}

#About2 .description h3 {
    margin-bottom: 0;
    color: rgb(66, 66, 66);
}

#About2 .description .job {
    margin-top: .3rem;
}


#About2 .description .user_box i {
    display: none;
}

#About2 .text_box {
    position: relative;
    padding: 3rem 4rem;
    border-radius: 1rem;
    font-size: clamp(1rem, 1.3vw, 3vw);
    line-height: 1.6rem;
    background-color: white;
    color: rgb(94, 94, 94);
    /* color: red; */
}

#About2 .text_box p {
    margin: 0;
}

#About2 .text_box i {
    position: absolute;
    top: .50rem;
    left: .50rem;
    font-size: 2.5rem;
}




@media (max-width: 1100px) {
    #About2 .section2 {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    #About2 .description {
        position: relative;
        top: 0rem;
        width: 100%;
        background-color: white;
    }

    #About2 .text_box {
        padding: 1rem 1rem 0 1rem;
    }

    #About2 .image_box {
        border: none; 
        width: 100%;
    }

    #About2 .image_box img {
        object-fit: contain;
        width: 100%;
    }

    #About2 .text_box i {
        display: none;   
    }



    #About2 .description .user_box {
        display: flex;
        align-items: center;
        gap: .5rem;
    }

    #About2 .description .user_box i {
        font-size: 3.5rem;
        display: block;
        color: gray;
    }
    
}


@media (max-width: 501px) {
    #About2 .about_containt {
        width: 95%;
    }
}



