#Faq2 .Faq_page_containter {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    padding: 5rem 0;
    margin-top: 5rem;
}

#Faq2 .Faq_page_containter h2{
    width: 80%;
    font-size: 2.2rem;
    margin-bottom: 4rem;
}

#Faq2 .Faq_page_containter .faq_containt {
    display: flex;
    justify-content: center;
    width: 80%;
    gap: 2rem;
    margin-bottom: 7rem;

}

#Faq2 .Faq_page_containter .faq_containt img {
    object-fit: cover;
    width: 45%;
    height: 22rem;
}

#Faq2 .Faq_page_containter .faq_containt .questions_list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

#Faq2 .Faq_page_containter .faq_containt .single_question {
    overflow: hidden;
    width: 100%;
    transition: .25s;
    border: 1px solid rgba(128, 128, 128, 0.539);
}

#Faq2 .Faq_page_containter .faq_containt .single_question.active .answer {
    position: relative;
    height: auto;
    height: 7rem;
}

#Faq2 .Faq_page_containter .faq_containt .single_question .answer.active {
    height: 14rem;
}

#Faq2 .Faq_page_containter .faq_containt .single_question.active .question {
    border-bottom: 1px solid rgba(128, 128, 128, 0.539);

}


#Faq2 .Faq_page_containter .faq_containt .question {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: .71rem;
    padding: 0 .6rem;
    padding-right: 2rem;
    background-color: rgba(128, 128, 128, 0.242);
}


#Faq2 .Faq_page_containter .faq_containt .question i {
    cursor: pointer;
    font-size: 1.52rem;
}

#Faq2 .Faq_page_containter .faq_containt .question i:hover {
   color: #f8a555;

}

#Faq2 .Faq_page_containter .faq_containt h3 {
    font-size: 1.2rem;
    padding: .6rem 0rem;
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: rgb(40, 40, 40);
}


#Faq2 .Faq_page_containter .faq_containt p {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    -webkit-box-orient: vertical;
    font-size: clamp(1rem, 1.3vw, 10vw);
    line-height: 1.6;
    color: rgb(48, 48, 48);
    line-height: 1.72rem;
}

#Faq2 .Faq_page_containter .faq_containt .answer.active p {
    -webkit-line-clamp: 200; 
   
}

#Faq2 .Faq_page_containter .faq_containt .answer {
    display: flex;
    overflow: hidden;
    height: 0;
    transition: .5s;

}

#Faq2 .Faq_page_containter .faq_containt .answer p span {
    color: rgba(0, 106, 255, 0.792);
    cursor: pointer;
    text-decoration: underline;
}

#Faq2 .Faq_page_containter .faq_containt .single_question .Read_more {
    position: absolute;
    text-decoration: underline;
    cursor: pointer;
    bottom: 1.1rem;
    right: .51rem;
    color: rgba(0, 106, 255, 0.792);
    margin: 0;
    background-color: white;
}


@media  (max-width: 1100px) {
    #Faq2 .Faq_page_containter h2 {
        width: 90%;
    }


    #Faq2 .Faq_page_containter .faq_containt {
        width: 90%; 
    }
}

@media (max-width: 1000px) {
    #Faq2 .Faq_page_containter .faq_containt img {
        display: none;
    }

    #Faq2 .Faq_page_containter {
        padding: 0 !important;
        margin: 0;
        width: 100vw;
        transform: translateX(-.71rem);
    }

    #Faq2 .Faq_page_containter .faq_containt .questions_list {
        width: 100%;
    }

    #Faq2 .Faq_page_containter h2 {
        margin-bottom: 3rem;
        font-size: clamp(1rem, 7vw, 2.5rem) !important;
    }
}




@media (max-width: 501px) {
    #Faq2 .Faq_page_containter .faq_containt .single_question .answer.active {
        height: auto;
        height: 20rem;
        height: 100%;
    }
}



