#Team3 .team_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    background-color: rgba(173, 216, 230, 0.412);

}

#Team3 .team_containt {
    width: 90%;
}








/* style for the title section */
#Team3 .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 4rem;
    margin-bottom: 4rem;
}

#Team3 .title_section h1 {
    font-size: clamp(1.5rem, 3vw, 10vw);
    margin: .5rem 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',  sans-serif;

}


#Team3 .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.3vw, 10vw);
    line-height: 1.6;
    width: 90%;
    margin: 0;
    color: rgb(100, 100, 100);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',  sans-serif;

    /* color: red; */
}



#Team3 .team_list {
    display: grid;
    grid-template-columns: repeat(auto-fill, 18rem);
    justify-content: center;
    align-items: center;
    gap: 1rem;
    row-gap: 3rem;
}

#Team3 .single_team {
    padding-bottom: 1rem;
    border-radius: 1rem;
    border: .7rem solid white;
}

#Team3 .single_team .img_box {
    overflow: hidden;
   width: 100%;
   height: 20rem;
}

#Team3 .single_team .img_box img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: .51rem;
    transition: .6s;
}

#Team3 .single_team:hover img {
    scale: 1.1;
}

#Team3 .description h3{
    text-align: center;
    font-size: clamp(1.4rem, 1.7vw, 4vw);
    margin: 0;
    margin-top: .5rem;
    color: rgb(47, 47, 47);
}

#Team3 .description p {
    text-align: center;
    margin: .5rem 0rem;
    color: rgb(77, 77, 77);
}

#Team3 .icons_section {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    font-size: 1.1rem;
    padding-top: .6rem;
    color: rgb(85, 85, 85);
}

#Team3 .icons_section i {
    cursor: pointer;
}

#Team3 .icons_section i:hover {
    scale: 1.4;
}

@media (max-width: 501px) {
    #Team3 .team_list {
        grid-template-columns: repeat(auto-fill, 80vw);
    }
}

