#slider1 {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 10rem;
    overflow: hidden;
}


#slider1 .slider_container {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid green;
    overflow: hidden;
}

#slider1 .slider_container .slider_list {
    position: relative;
    display: flex;
    width: 50vw;                                /* to modify the size of the slide */  
    height: 26rem;
    border: 1px solid red;
    
    /* if the number of slide is <= 3 or if you want a single slide to be seen at a time*/
    /* overflow: hidden; */
}

#slider1 .slider_container .single_slide_box {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    transition: .251s;


    /* if the number fo slide is === 4*/
    /* transform: translateX(50%);              */

    /* if the number of slide is <= 3 */
    /* transform: translateX(100%); */
    
    
}

#slider1 .slider_container .single_slide {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 95%;
    height: 90%;
    background-color: rgb(159, 243, 159);

}