/* NavBar style */
#Navbar2 {
    /* position: fixed; */
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    z-index: 10;
    width: 100vw;
    background-color: white;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.236);
    /* display: none; */
}

#Navbar2 .navbar_containt {
    display: flex;
    justify-content: space-between;
    width: 90%;
}


/* Logo section, right section in the navbar */
#Navbar2 .navbar_containt .logo_section {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#Navbar2 .navbar_containt .logo_section h2 {
    margin: .7rem 0;
}

#Navbar2 .navbar_containt .logo_section .links {
    display: none;
}

#Navbar2 .navbar_containt .bi-search {
    display: none;
}


/* Center section in the navbar */

#Navbar2 .navbar_containt ul {
    display: flex;
    align-items: center;
    gap: 2rem;
}

#Navbar2 .navbar_containt ul li {
    list-style-type: none;
    cursor: pointer;
}

#Navbar2 .navbar_containt li a {
    text-decoration: none;
    color: rgb(39, 39, 39);
    font-size: 1.1rem;
}

/* Shop section, right section in the navbar */

#Navbar2 .navbar_containt .shop_section {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;
}

#Navbar2 .navbar_containt .shop_section button {
    padding: .71rem 1.8rem;
    font-size: 1rem;
    border-radius: 10rem;
    border: none;
    font-weight: bold;
    background-color: #3b61dd;
    color: white;
    cursor: pointer;
}

#Navbar2 .navbar_containt .shop_section i {
    font-size: 1.6rem;
    cursor: pointer;
}


/* Media query, responsivity for screen from 1050px */
@media (max-width: 1050px) {
    #Navbar2 .navbar_containt {
        flex-direction: column;
    }

    #Navbar2 .navbar_containt .shop_section {
        display: none;
    }


    #Navbar2 .navbar_containt .logo_section .links {
        display: flex;
        justify-content: space-between;
        gap: 1.52rem;
    }

    #Navbar2 .navbar_containt .logo_section button {
        padding: .51rem 1.4rem;
        font-size: .81rem;
        border-radius: 10rem;
        border: none;
        font-weight: bold;
        background-color: #3b61dd;
        color: white;
        cursor: pointer;
    }

    #Navbar2 .navbar_containt .logo_section .links i {
        font-size: 1.6rem;
        cursor: pointer;
    }

    #Navbar2 .navbar_containt ul {
        flex-direction: column;
        padding: 0;
        gap: 0;
    }

    #Navbar2 .navbar_containt ul li {
        text-align: center;
        border-bottom: 1px solid rgb(228, 228, 228);
        width: 100%;
        padding: 1rem 0;
        cursor: pointer;
    }

    #Navbar2 .navbar_containt ul li:hover {
        background-color: rgb(237, 237, 237);
    }

    #Navbar2 {
        height: 3.4rem;
        align-items: start;
        transition: .5s;
        overflow: hidden;
    }


    #Navbar2.active {
        height: 100vh !important;
    }


}

/* Responsivity for smaller screen */

@media (max-width: 501px) {
    #Navbar2 .navbar_containt .links button {
        display: none;
    }

    #Navbar2 .navbar_containt .bi-search {
        display: block;
    }
}