#Hero_1 {
    position: relative;
    display: flex;
    justify-content: center;
    position: relative;
    color: white;
    width: 100vw;
    height: 100vh;
    z-index: 1;

}

#Hero_1 .header_background {
    position: absolute;
    background-color: black;
    inset: 0;
    z-index: -1;
}



#Hero_1 .img_background_box {
    position: absolute;
    overflow: hidden;
    width: 100%;
    height: 100%;
    z-index: -1;
}


#Hero_1 .image_background_containt {
    animation: hero ease-in-out 30s infinite;
    height: 100vh;
}


#Hero_1 .hero_background {
    object-fit: cover;
    width: 100%;
    height: 100%;
    filter: brightness(40%);
}

#Hero_1 .hide_img {
    animation: hide_img ease 2s forwards;
}

@keyframes hide_img {
    0% {opacity: 1;}
    100% {opacity: 0;}
}

#Hero_1 .show_img {
    animation: show_img ease 2s forwards;
}

@keyframes show_img {
    0% {opacity: 0;}
    100% {opacity: 1;}
}


@keyframes hero {
    0% {
        scale: 1;
    }

    50% {
        scale: 1.3;
    }

    100% {
        scale: 1;
    }
}


#Hero_1 .hero_containt {
    display: flex;
    align-items: center;
    width: 85%;
    height: 90vh;
}

#Hero_1 .hero_containt .details_section {
    width: 100%;
}

#Hero_1 .hero_containt  .stars i {
    margin: 0 .4rem;
    font-size: 1.2rem;
    color: gold;
} 

#Hero_1 .hero_containt h1 {
    font-size: clamp(1.4rem, 4.7vw, 5vw);
    margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',  sans-serif;

}

#Hero_1 .hero_containt .details {
    width: 80%;
    font-size: 1.2rem;
    color: rgba(255, 255, 255, 0.671);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',  sans-serif;

}

#Hero_1 .hero_containt form {
    display: flex;
    flex-direction: column;
    width: 30vw;
    padding: 1.5rem;
    border-radius: .71rem;
    gap: .8rem;
    background-color: rgba(255, 255, 255, 0.76);
    border: 1px solid rgb(181, 181, 181);
}

#Hero_1 .hero_containt form .adult_children {
    display: flex;
    gap: 1rem;
}

#Hero_1 .hero_containt form .adult_children input {
    width: 100%;
}

#Hero_1 .hero_containt form label {
    color: rgb(63, 63, 63);
    font-size: 1.1rem;
    color: black;
}

#Hero_1 .hero_containt form input {
    border: none;
    width: 100%;
    height: 2.5rem;
    font-size: 1rem;
    border-radius: .5rem;
    margin-top: .8rem;
    background-color: rgb(227, 227, 227);
    background-color: white;
    border: 1px solid rgb(178, 178, 178);
}

#Hero_1 .hero_containt form input:focus {
    outline: none;
}

#Hero_1 .hero_containt form button {
    border: none;
    cursor: pointer;
    margin-top: 1rem;
    height: 2.5rem;
    border-radius: .5rem;
    font-size: 1.1rem;
    color: white;
    background-color: #3b61dd;
}


@media (max-width: 1000px) {
    #Hero_1 .hero_containt {
        flex-direction: column;
        padding-top: 14rem;
    }

    #Hero_1 .hero_containt form {
        width: 60vw;
        margin-top: 5rem;
        background-color: rgb(242, 242, 242);
        border: 1px solid rgb(226, 226, 226);
    }


    #Hero_1 .hero_containt form input {
        width: 95%;
    }

    #Hero_1 .hero_containt form .adult_children div {
         width: 50%;
    }

    #Hero_1 .hero_containt form .adult_children input {
        width: 90%;
    }

    #Hero_1 .hero_containt h1 {
        font-size: clamp(1.4rem, 7.7vw, 8vw);
    }

    
    #Hero_1 .hero_containt .details {
    width: 90%;

    font-size: clamp(.8rem, 1.5vw, 6vw);
}
    
}

@media (max-width: 501px) {
    #Hero_1 .header_background {
        height: 90vh;

    }


    #Hero_1 .hero_containt h1 {
        font-size: clamp(1.4rem, 9.3vw, 20vw);
    }

    #Hero_1 .hero_containt .details {
        margin-bottom: 2rem;
        width: 100%;
    }

    #Hero_1 .hero_containt form {
    background-color: rgb(237, 237, 237);
    border: 1px solid rgb(225, 225, 225);

    }

    #Hero_1 .hero_containt form input {
    height: 2rem;
    margin-top: .1rem;

    }

    #Hero_1 .hero_containt form label {
    font-size: .9rem;
    }

    #Hero_1 .img_background_box {
    height: 90vh;
    }

    #Hero_1 .hero_background {
    height: 90vh;
    }

}