#Footer4 .footer_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 4rem;
    padding-bottom: 6rem;
    background-color: rgb(15, 48, 53);
    background-color: rgb(20, 39, 58);
    color: white;
}

#Footer4 .footer_containt {
    display: grid;
    grid-template-columns: 5fr 3fr 3fr;
    gap: 1rem;
    width: 90%;
}



#Footer4 .footer_containt > div h3 {
    border-left: .3rem solid red;
    padding-left: 1rem;
    font-size: 1.5rem;
}

#Footer4 .footer_containt > div p {
    color: rgba(255, 255, 255, 0.767);
    line-height: 1.6rem;
    font-size: 1.2rem;
}

#Footer4 .footer_containt > div:last-child p {
    cursor: pointer;
}

#Footer4 .icon_box {
    display: flex;
    gap: 1rem;
}

#Footer4 .footer_containt > div i {
    cursor: pointer;
    font-size: 1.5rem;
}

#Footer4 .footer_containt > div img {
    width: 1.62rem;
    height: 1.62rem;
    border-radius: 10rem;
    object-fit: cover;
    cursor: pointer;


}


@media (max-width: 800px) {
    #Footer4 .footer_containt {
        grid-template-columns: 3fr 2fr;
    }
}

@media (max-width: 600px) {
    #Footer4 .footer_containt {
        grid-template-columns: 1fr;
        gap: 2rem;
    }
}

@media (max-width: 600px) {
    #Footer4 .footer_containt > div{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
}