#Navbar3 {
    width: 100vw;
    overflow: hidden;
}

#Navbar3 .navbar_1_box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    background-color: rgb(28, 28, 39);
}

#Navbar3 .navbar_1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.5rem;
    width: 90%;
    height: 3.5rem;
    color: white;
}




/* ///////////////////////////////////////////////////////////////////// */
/* Logo section */

#Navbar3 .navbar_1 .logo_section h1 {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: clamp(1.5rem, 2vw, 10vw);
}

#Navbar3 .navbar_1 .logo_section h1 span {
    color: orangered;
}




/* //////////////////////////////////////////////////////////////// */
/* Search section */

#Navbar3 .navbar_1 .search_section {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
}

#Navbar3 .navbar_1 .search_section .search_box {
    display: flex;
    justify-content: center;
    overflow: hidden;
    width: 100%;
    height: 2.23rem;
    border-radius: .41rem;
    color: white;
}

#Navbar3 .navbar_1 .search_box input {
    border: none;
    font-size: 1rem;
    width: 100%;
   }

#Navbar3 .navbar_1 .search_box input:focus {
    outline: none;
}

#Navbar3 .navbar_1 .search_box i {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    gap: .5rem;
    font-size: 1rem;
    width: 8rem;
    background-color: orangered;
    border: 1px solid orangered;
}




/* ///////////////////////////////////////////////////////////////// */
/* cart_section */

#Navbar3 .navbar_1 .cart_section {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}


#Navbar3 .navbar_1 .cart_section .box_1 {
    position: relative;
}

#Navbar3 .navbar_1 .cart_section .box_1 i {
    cursor: pointer;
    font-size: 1.5rem;
}

#Navbar3 .navbar_1 .cart_section .box_1 p {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: -1.4rem;
    right: -1rem;
    width: 1.4rem;
    height: 1.4rem;
    border-radius: 10rem;
    background-color: rgba(255, 255, 255, 0.182);
    color: white;
}





/* Media queries */


@media (max-width: 700px) {
    #Navbar3 .navbar_1 .search_section .search_box {
       justify-content: end;
    }

    #Navbar3 .navbar_1 .search_box input {
        width: 0%;
        height: 0rem;
        padding: 0;
    }

    #Navbar3 .navbar_1 .search_box i {
        display: flex;
        justify-content: center;
        align-items: center;
        border: none;
        gap: .0rem;
        width: 2.2rem;
        height: 2.2rem;
        border-radius: 10rem;
        background-color: rgba(255, 255, 255, 0.221);
    }

    #Navbar3 .navbar_1 .search_box i span {
        display: none
    };



    /* //////////////////////////////////////////////////////////////// */
    /* Show and hide input for mobile responsivity */

   #Navbar3 .navbar_1.active .logo_section {
    display: none ;
   }

   #Navbar3 .navbar_1.active .search_section {
    width: 100%;
   }

   #Navbar3 .navbar_1.active .search_box input {
    width: 100%;
    height: auto;
   }

   #Navbar3 .navbar_1.active .search_box i {
    border-radius: 0rem;
    width: 5rem;
    background-color: orangered;
    border: 1px solid orangered;
   }

   #Navbar3 .navbar_1.active .cart_section {
       display: none;
   }


   #Navbar3 .navbar_1.active .logo_section {
       display: none ;
      }
}




@media (max-width: 400px) {
    #Navbar3 .navbar_1 {
        gap: .5rem;
    }


    #Navbar3 .navbar_1 .logo_section h1 {
        font-size: clamp(1.2rem, 2vw, 10vw);
    }

    #Navbar3 .navbar_1 i {
        scale: .8 ;
    }

    #Navbar3 .navbar_1 .cart_section .box_1 {
        scale: .8 !important;
    }

    #Navbar3 .navbar_1 .cart_section {
       gap: .7rem;
       width: 45%;
    }

    #Navbar3 .navbar_1 .search_box i {
        scale: 1 !important;
    }


}





/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* Navbar 2 */

#Navbar3 .Navbar_2_box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    color: white;
    background-color: rgb(45, 45, 100);
}

#Navbar3 .Navbar_2 {
    display: flex;
    align-items: center;
    justify-content: start;
    gap: 2rem;
    width: 90%;
    height: 2.5rem;
}

#Navbar3 .Navbar_2 .bi-filter-left {
    font-size: 2rem;
    cursor: pointer;

}

#Navbar3 .Navbar_2 ul {
    display: flex;
    gap: 1.5rem;
    margin: 0;
}

#Navbar3 .Navbar_2 ul li {
    list-style-type: none;
    cursor: pointer;
}


@media (max-width: 400px) {
    #Navbar3 .Navbar_2 {
        gap: .41rem !important;
        height: 2rem;
    }

    #Navbar3 .Navbar_2 .bi-filter-left {
        font-size: 1.5rem;
    }

    #Navbar3 .Navbar_2 ul {
        gap: 1rem;
    }

    #Navbar3 .Navbar_2 ul li a {
        font-size: .8rem;
    }
}


