#List_btn .list_container {
    position: fixed;
    width: 50vw;
    top: 3rem;
    right: 4rem;
    transition: .5s;
    z-index: 100;

}

#List_btn .list_container.active {
    width: 5rem;
}

#List_btn .list_container .component_box {
    overflow-y: auto;
    width: 100%;
    background-color: rgb(233, 245, 253);
    min-height: 55vh;
}

#List_btn .list_container .component_box .home_link {
    font-weight: bold !important;
}

#List_btn .list_container ul {
    display: flex;
    flex-direction: column;
    align-items: center;   
    overflow: hidden;
    margin: 0;
    padding: 0;
    transition: .5s;
    max-height: 70vh;
    min-height: 70vh;
    border: 1px solid rgba(128, 128, 128, 0.387);
    background-color: #111d49;   
}

#List_btn .list_container ul.active {
    height: 0;
    padding: 0;
    margin: 0;
    width: 0;
    border: none;
}

#List_btn .list_container .list_btn_header {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 6rem;
}

#List_btn .list_container ul h1 {
    font-size: clamp(1.5rem, 1.72vw, 10rem);
    color: rgb(26, 53, 103);
    color: white;
    width: 90%;
    text-align: center;
    margin: 0rem;
    margin-bottom: .5rem;
}

#List_btn .list_container input {
    width: 70%;
    height: 1.72rem;
    border-radius: 10rem;
    padding-left: 1.52rem;
    border: none;
    font-size: 1.2rem;
    font-size: .8rem;
    font-size: clamp(.8rem, 1.2vw, 4rem);
    text-align: center;
    /* outline: auto; */
}

#List_btn .list_container ul li {
    padding: .61rem 0;
    width: 100%;
    text-align: center;
    list-style-type: none;
    border-bottom: 1px solid rgba(128, 128, 128, 0.227);
    font-weight: 400;
    font-size: 1.15rem;
    color: black;
}


#List_btn .list_container ul li:hover {
    background-color: rgba(29, 229, 49, 0.301);
    background-color: rgb(156, 211, 230);
    background-color: #3b61dd21;
    

}

#List_btn .list_container ul .Link {
    text-decoration: none;
    color: black;
    font-weight: 500;
    width: 100%;
    
 }

#List_btn .home_link {
    font-weight: bold !important;
    color: red;
 }

 #List_btn .btn_section {
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: end;
    width: 100%;
 }

 #List_btn .btn_section .bi-toggle-on,
    #List_btn .btn_section .bi-toggle-off {
    color: #103de3;
    cursor: pointer;
    font-size: 1.3rem;
    transform: translate(-.51rem, 0rem);
    opacity: .3;
 }

#List_btn .list_container button {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10rem;
    width: 2.4rem;
    height: 2.4rem;
    text-align: center;
    cursor: pointer;
    border: 1px solid rgba(0, 0, 0, 0.299);
    color: black;
    background-color: rgba(29, 229, 49, 0.136);
    background-color: rgba(173, 216, 230, 0.389);
} 

#List_btn .list_container button i {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    width: 2rem;
    height: 2rem;
}

#List_btn .list_container button .bi-x-lg {
    color: red;
}

#List_btn .All_comonents {
    position: fixed;
    z-index: 102;
    bottom: 2rem;
    right: -15rem;
    padding: 0 1rem;
    min-width: 8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    height: 1.82rem;
    border-radius: 10rem;
    background-color: rgba(173, 216, 230, 0.389);
    transform: translateX(-160%);
    border: 1px solid #111d49;


}

#List_btn .All_comonents .button {
    text-decoration: none;
    color: black;
    font-weight: bold;
}


@media (max-width: 630px) {
    #List_btn .list_container {
        width: 90vw;
        right: 1rem;
    }



}