#Skill6 .service_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding: 2rem 0;
}

#Skill6 .service_containt {
    width: 80%;
}





#Skill6 .service_containt > h2 {
    position: relative;
    font-size: clamp(1.4rem, 2.2vw, 3vw);
}

#Skill6 .service_containt > h2::before,
#Skill6 .service_containt > h2::after {
    content: "";
    position: absolute;
    width: 5rem;
    height: .152rem;
    background-color: #3b61dd;
    border-radius: 1rem;
    bottom: -0.2rem;
    left: 0;
}

#Skill6 .service_containt > h2::after {
   bottom: -.7rem;
   width: 3rem;
   
}






#Skill6 .service_containt > div {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    box-sizing: border-box;
    padding: 1rem 0;
    gap: 1rem;
}

#Skill6 .service_containt > div > div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2rem .4rem;
    border-radius: 1rem;
    box-shadow: 0 0 1rem  rgba(0, 0, 0, 0.185);
    background-color: white;
}

#Skill6 .service_containt > div > div:hover {
        box-shadow: 0 0 1rem inset  rgba(0, 0, 0, 0.185);

}

#Skill6 .service_containt > div > div h4 {
    text-align: center;
    margin-bottom: 0;
    margin-top: .5rem;
    font-size: clamp(.7rem, 1.2vw, 5vw);
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: rgba(0, 0, 0, 0.688);
}

#Skill6 .service_containt > div > div img {
    object-fit: contain;
    height: 4rem;
}

@media (max-width: 800px) {
    #Skill6 .service_containt > div {
    grid-template-columns: 1fr 1fr 1fr;
}

}

@media (max-width: 600px) {
    #Skill6 .service_containt > div {
    grid-template-columns: 1fr 1fr;
}

}

@media (max-width: 400px) {
    #Skill6 .service_containt {
        width: 90%;
    }

    #Skill6 .service_containt > div {
    gap: .5rem;
}
}

@media (max-width: 300px) {
    #Skill6 .service_containt > div > div img {
    height: 3rem;
}
}


