#Service3 .Service_section_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding: 7rem 0;
}



#Service3 .Service_section_containt {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(22rem, 20vw));
    justify-content: center;
    width: 90%;
    gap: 2rem;
    row-gap: 5rem;
}


#Service3 .services_header_page {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-bottom: 3rem;
}

#Service3 .service_header_containt {
    width: 80%;
}


#Service3 .service_header_containt h2 {
    font-size: 2rem;
}

#Service3 .service_header_containt p {
    font-size: 1.18rem;
    line-height: 1.8rem;
    color: rgb(69, 69, 69);
}


#Service3 .Service_section_containt .slider_1 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
    text-decoration: none;
    transition: .5s;
    box-shadow: 0 0 1rem rgba(128, 128, 128, 0.516);
    padding-bottom: 1rem;
    background-color: white;

}

#Service3 .Service_section_containt .slider_1 .slider_1_box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}


#Service3 .Service_section_containt .slider_1 img {
    object-fit: cover;
    width: 105%;
    height: 14rem;
}

#Service3 .Service_section_containt .slider_1:hover {
    scale: 1.05;
}

#Service3 #service1_title {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 103%;
    background-color: #1C2755;
}

#Service3 .Service_section_containt .service_title h3{
    font-size: clamp( 1rem , 1.2vw, 3vw);
   margin: .5rem 0;
   width: 90%;
   color: white;
} 

#Service3 .Service_section_containt .description {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 92%;
}

#Service3 .Service_section_containt .slider_1 p {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    -webkit-box-orient: vertical;
    font-size: clamp(1rem, 1.35vw, 3vw);
    padding-right: .6rem;
    color: rgb(60, 60, 60);
}

#Service3 .Service_section_containt .slider_1 .btn {
     width: 100%;
}

#Service3 .Service_section_containt .slider_1 button {
    border: none;
    font-weight: bold;
    cursor: pointer;
    padding: .6rem 1rem;
    font-size: .93rem;
    transition: .25s;
    border-radius: .51rem;
    background-color: #f8a555;
    color: white;
}

#Service3 .Service_section_containt .slider_1 button:hover {
    padding: .6rem 2rem;
    border-radius: 0;
}

@media (max-width: 501px) {
    #Service3 .service_header_containt {
        width: 90%;
    }

    #Service3 .service_header_containt h2 {
        font-size: clamp(1.2rem, 5vw, 2rem);
    }

    #Service3 .Service_section_containt {
        grid-template-columns: repeat(auto-fill, 90vw);

    }
}

