#Testimonial3 .Feedback {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 10rem;
    /* padding-bottom: 20rem; */
    background-color: rgba(173, 216, 230, 0.412);

}

#Testimonial3 .feedback_containt {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

/* style for the title section */
#Testimonial3 .title_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 4rem;
    margin-bottom: 4rem;
}

#Testimonial3 .title_section h1 {
    font-size: clamp(1.5rem, 2.63vw, 10vw);
    margin: .5rem 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',  sans-serif;

}


#Testimonial3 .title_section p {
    text-align: center;
    font-size: clamp(1rem, 1.3vw, 10vw);
    line-height: 1.6;
    width: 80%;
    margin: 0;
    color: rgb(100, 100, 100);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',  sans-serif;
   margin-bottom: 5rem;
    /* color: red; */
}




/* Feedbacks list styles */
#Testimonial3 .feedback_list_box {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* border: 1px solid red; */
}


#Testimonial3 .feedback_list_box:hover .bi-chevron-left
#Testimonial3 .feedback_list_box:hover .bi-chevron-right {
    opacity: 1 ;
    display: none;
}




#Testimonial3 .Feedback .feedbacks_list {
    display: flex;
    width: 50vw;
}

#Testimonial3 .Feedback .single_feedback_box {
    /* position: relative; */
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 50vw;
    transition: .5s;
    /* border: 1px solid green; */
    /* transform: translateX(-100%); */
}



#Testimonial3 .Feedback .single_feedback {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90%;
}

#Testimonial3 .Feedback .single_feedback img {
    width: 5rem;
    height: 5rem;
    border-radius: 10rem;
    object-fit: cover;
}

#Testimonial3 .Feedback .single_feedback h3 {
    font-size: 1.5rem;
    color: rgb(87, 87, 87);
    margin: .6rem 0;
    margin-bottom: 0;
}

#Testimonial3 .Feedback .single_feedback .position {
    color: gray;
    margin-top: .5rem;
    font-size: 1.15rem;
}

#Testimonial3 .Feedback .single_feedback .location {
    margin-top: 0.5rem;
    margin-bottom: 2rem;
    color: gray;
}

#Testimonial3 .Feedback .description {
   text-align: center;
   background-color: white;
   padding: 1rem;
   border-radius: .71rem;
}

#Testimonial3 .Feedback .description p {
    font-size: clamp(1rem, 1.25vw, 10vw);
    color: rgba(46, 98, 144, 0.872);
    line-height: 1.7rem;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 4; 
    -webkit-box-orient: vertical;
}

#Testimonial3 .Feedback .description i {
    font-size: 1.1rem;
    margin: 0 .2rem;
    color: #F1C40F  ;
}

#Testimonial3 .Feedback .control_btns {
    width: 43vw;
    height: 23rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transform: translateY(-23rem);
    opacity: 0;
    transition: .25s;
}

#Testimonial3 .Feedback .control_btns:hover {
    opacity: .7;
}

#Testimonial3 .Feedback .bi-chevron-left,
#Testimonial3 .Feedback .bi-chevron-right {
   /* position: absolute; */
   display: flex;
   justify-content: center;
   align-items: center;
   z-index: 10;
   background-color: #3b61dd;
   width: 3.5vw;
   height: 3.5vw;
   min-width: 2rem;
   min-height: 2rem;
   font-size: clamp(1.5rem, 2vw, 5vw);
   border-radius: 10rem;
   color: white;
   cursor: pointer;
   transform: translateY(-1rem);
}

#Testimonial3 .Feedback .bi-chevron-left {
    left: 28vw;
}

#Testimonial3 .Feedback .bi-chevron-right {
    right: 28vw;

}



@media (max-width: 800px) {
    #Testimonial3 .Feedback .feedbacks_list {
        width: 98vw;
    }

    #Testimonial3 .Feedback .single_feedback_box {
        min-width: 98vw;

    }

    #Testimonial3 .Feedback .control_btns {
        width: 90vw;
        opacity: .7;
    }
}

