#About3 .Mission_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding:  6rem 0;
    background-color: rgba(128, 128, 128, 0.201);
}

#About3 .mission_containt {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4rem;
    width: 80%;
}

 #About3 .mission_containt .details {
     display: flex;
    width: 100%;
    gap: 1rem;
}

#About3 .mission_containt h3 {
    margin-top: 0;
    font-size: 1.6rem;
 
}

#About3 .mission_containt p {
    width: 60%;
    font-size: clamp(1rem, 1.3vw, 5vw);
    margin: 0;
    line-height: 1.6;
    color: rgba(0, 0, 0, 0.755);
}

#About3 .mission_containt img {
    object-fit: cover;
    width: 40%;
    border: .5rem solid white;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.25);
}

@media (max-width: 1100px) {
    #About3 .mission_containt {
        width: 90%;
    }
}

@media (max-width: 950px) {
    #About3 .mission_containt {
        flex-direction: column;
    }
}

@media (max-width: 501px) {
    #About3 .mission_containt .details {
        flex-direction: column !important;
        gap: 2rem;
    }

    #About3 .mission_containt p {
        width: 90%;
    }

    #About3 .mission_containt img {
        width: 60vw;
    }
}




