#Faq3 .faq_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    margin-bottom: 50rem;
    box-sizing: border-box;
    background-color: rgba(173, 216, 230, 0.412);

    
}

#Faq3 .faq_containt {
    width: 80%;
    box-sizing: border-box;
}



#Faq3 .title_section {
    display: flex;
   flex-direction: column;
   align-items: center;
   text-align: center;
   margin-top: 10rem;
   margin-bottom: 10rem;

}

#Faq3 .title_section h1 {
   font-size: clamp(1.7rem, 4vw, 10vw);
   margin: 1rem 0;
   color: rgb(23, 23, 23);
}



#Faq3 .title_section p {
   text-align: center;
    font-size: clamp(1rem, 1.3vw, 10vw);
    line-height: 1.6;
   width: 90%;
   margin: .5rem;
   color: rgb(100, 100, 100);
}

#Faq3 .title_section button {
     cursor: pointer;
   border: none;
   width: 15rem;
   padding: .71rem 0;
   border-radius: 10rem;
   font-size: 1rem;
   margin-top: 1rem;
   border: .7rem solid white;
   background-color: #3b61dd;
   color: white;
}







/* //////////////////////////////////////////////// */

#Faq3 .questions_container {
    display: flex;
    gap: 1rem;
}


#Faq3 .side_bar_box {
    width: 30%;
}


#Faq3 .side_bar_containt {
    background-color: white;
    border-radius: 1rem;
    padding: 0.1rem 2rem;

}

#Faq3 .side_bar_containt a {
    text-decoration: none;
}

#Faq3 .side_bar_containt h5 {
    cursor: pointer;
    color: rgb(69, 69, 69);
    font-size: 1.2rem;
}



#Faq3 .single_list_containt {
    border: 1px solid rgba(0, 0, 255, 0.109);
    background-color: white;
    color: rgb(69, 83, 104);
    border-radius: 1rem;
}

#Faq3 .single_list_containt h5 {
    margin: 0;
    padding: 1rem 2rem;
    cursor: pointer;
    border-bottom: 1px solid rgba(0, 0, 255, 0.115);
    font-size: 1.2rem;
}






#Faq3 .single_list_containt p {
    margin: 0;
    overflow: hidden;
    height: 0;
    padding: 0;
    padding: 0rem 2rem;
    border: none;
    transition: .51s;
    font-size: 1.1rem;
    line-height: 1.5rem;
}


#Faq3 .single_list_containt p.active {
    height: 4rem;
    height: auto;   /*/////////////////////////////////////////////////////////////////////*/
    padding: 1rem 2rem;
    border-bottom: 1px solid rgba(0, 0, 255, 0.115);

}




#Faq3 .question_list {
    width: 70%;
}


#Faq3 h3 {
    /* color: red; */
    /* font-size: 1.7rem; */
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',  sans-serif;

}


@media (max-width: 800px) {
    #Faq3 .questions_container {
        flex-direction: column;
    }

    #Faq3 .side_bar_box {
    width: 100%;
}

#Faq3 .question_list {
    width: 100%;
}

}