#Galerie1 .welcom_galerie {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    inset: 0;
    background-color: rgba(0, 0, 0, 0.806);
    z-index: 999;
}

#Galerie1 .welcom_galerie_containt {
    position: relative;
    width: 70vw;
    height: 70vh;
    /* border: 3px solid red; */
}


#Galerie1 .welcom_galerie_containt .image_box {
    position: absolute;
    width: 100%;
    height: 100%;

}

#Galerie1 .welcom_galerie_containt .image_box h1 {
    position: absolute;
    z-index: 9999;
    background-color: orange;
    border-radius: .3rem;
    top: 0rem;
    right: 1.5rem;
    padding: .2rem .8rem;
    font-size: 1.2rem;
    min-width: 10rem;
    text-align: center;
}

/* .welcom_galerie_containt .image_box .bi-fullscreen {
    position: absolute;
    font-size: 3rem;
    top: 50%;
    left: 50%;
    color: white;
    transform: translate(-50%, -50%);
} */


#Galerie1 .welcom_galerie_containt .image_box img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0;
    transition: .2s;
}


#Galerie1 .welcom_galerie_containt .bi-chevron-left ,
#Galerie1 .welcom_galerie_containt .bi-chevron-right {
    position: fixed;
    font-size: 3rem;
    top: 40%;
    border: none;
    color: white;
    cursor: pointer;
    /* border: 1px solid red; */
    width: 4rem;
    height: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10rem;
    background-color: rgba(0, 0, 0, 0.527);
}

#Galerie1 .welcom_galerie_containt .bi-chevron-left {
    left: 3rem;
    
}

#Galerie1 .welcom_galerie_containt .bi-chevron-right {
   right: 3rem;
}

#Galerie1 .welcom_galerie_containt .galerie_header {
    position: absolute;
    display: flex;
    gap: 2rem;
    top: -2.5rem;
    right: -0rem;
    font-size: 1.5rem;
    color: white;
    cursor: pointer;
    transition: .5s;
}



#Galerie1 .welcom_galerie_containt .show_image {
    animation: show_images .51s ease forwards;

}

#Galerie1 .welcom_galerie_containt .hide_image {
    animation: hide_images .51s ease forwards;

}

@keyframes show_images {
    0% {opacity: 0;}
    100%{opacity: 1;}
}

@keyframes hide_images {
    0% {opacity: 1;}
    100%{opacity: 0;}
}





