#About3 .History_container {
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateX(-1rem);
    width: 100vw;
    padding: 7rem 0;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.124);
    border: 1px solid rgba(211, 210, 210, 0.709);
}

#About3 .history_containt {
    display: flex;
    width: 80%;
    gap: 2rem;
}

#About3 .history_containt .description {
     width: 60%;
} 

#About3 .history_containt h3 {
    font-size: 2rem;
    margin-top: 0;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

#About3 .history_containt p {
    line-height: 1.6;
    color: rgba(0, 0, 0, 0.712);
    /* color: red; */
    font-size: clamp(1rem, 1.3vw, 5vw) !important;
    font-size: 5.3rem;
}

#About3 .history_containt img {
    object-fit: cover;
    width: 40%;
    min-width: 25rem;
    border: .5rem solid white;
    box-shadow: 0 0 2rem rgba(0, 0, 0, 0.17);
}


@media (max-width: 1000px) {
    #About3 .history_containt {
        flex-direction: column;
    }

    #About3 .history_containt .description {
        width: 100%;
    }

    #About3 .history_containt img {
        width: 100%;
    }
}

@media (max-width: 501px) {
    #About3 .history_containt img {
        min-width: 0rem;
    }
}